const EmailChip = (props) => {
   return (
      <span 
         class={ props.class ? "chip " + props.class : "chip"} 
         value={ props.value }
         onClick={ props.onClick }
      >
         { props.children }
         <a 
            class="btn btn-clear" 
            aria-label="Close" 
            role="button" 
         />
      </span>
   )
}

export { 
   EmailChip 
};




